import React from 'react';
import "../styles.css";

export const Section = (props)=>{
    let path='/static/images/Collage/'+props.title+"/"+props.img;
    //console.log(path);
    let toReturn  = (
        <a className="collageGrid" href = {props.href}>
            <p className= "collageTitle">{props.title}</p>
            <img className = "collageImg" alt="Drone" src = {path}></img>
        </a>
    );
    //console.log(props.title);
    return toReturn;
}