import React from 'react';
import ReactDOM from 'react-dom';
import {Header} from './Header/Header';
import {WritingText} from './Writing&Title/WritingText';
import {Collage} from './Collage/Collage';

//header
let navbarList = ['Projects', 'About', 'Contact'];
ReactDOM.render(
    <Header list = {navbarList} name = "AMRITPAL CHERA" TitleHref = "/"/>,
    document.getElementById('header')
);

//title with text (template for 10 things)
let aboutText = [
    
    'I like to build circuits and put together parts of a meaningful mechatronics system',
    'I like to play sports, stay healthy, and eat healthy',
    'I like to continuously learn new things',
    'I like to live a simple life',
    'I like programming in different languages',
    'I am always eager to program in a new way using different tools for each project',
    "I am passionate to master implementing AI in the near future",
    "I am passionate about large scale projects on renewable energy",
    "I am passianate about new and innovate technologies",
    
    'Favorite quote: "Started At Nothing! Stopping At Nothing! NEVER SETTLE"'
];

let contactText = [
    "Email: achera@uwaterloo.ca"
]

ReactDOM.render(
    <WritingText title ="ABOUT ME" text={aboutText} ListView = {true} subTitle = "Summary of The Things I Value:" TitleHref = "/about"/>,
    document.getElementById('aboutSummary')
);

//projects page
ReactDOM.render(
    <Collage title = "KEY PROJECTS" TitleHref = "/projects" ContentHref = "/projects"/>,
    document.getElementById('projectSummary')
);

//Contact Page
ReactDOM.render(
    <WritingText title = "CONTACT" text = {contactText} TitleHref = "/contact"></WritingText>,
    document.getElementById('contact')
)




