import React from 'react';
import '../styles.css';

export const Title = (props)=>{
    let toReturn = null;
    props.subTitle? toReturn = (<a className = "subtitleSubpage" href = {props.href}>{props.subTitle} </a>):
    toReturn = (<a className= "titleSubpage" href = {props.href}>{props.name}</a>);
    return (toReturn);
}