import React from 'react';
import '../styles.css';
import {pictures} from '../images/Collage/data.js';
import {Section} from './Section';
import {Title} from './Title';

export class Collage extends React.Component{    
    render(){
        let toReturn = null;
        let divs = [];
        for (var element in pictures){
            //section returns a div with all 3 elements
            divs.push(<Section img = {pictures[element].img} title = {pictures[element].title} key ={element} href = {this.props.ContentHref + "#"+pictures[element].title}></Section>);                 
        }
        
        toReturn = divs.map((e)=>{
                return (e);//returns a div of canvas from an array
            });
        
        return(
        <div className ="collageElements">
            <Title name = {this.props.title} href = {this.props.TitleHref}></Title>
            <div className = "collageContainer">{toReturn}</div>
        </div>); //wrap a set of sections into one div
    }
}